"use client";

import { useState, useRef, useEffect } from "react";
import { useAgora } from "@/hooks/useAgora";
import { useRTM } from "@/hooks/useRTM";

interface Product {
  id: string;
  name: string;
}

interface ChatMessage {
  role: "user" | "assistant";
  content: string;
}

interface ChatInterfaceProps {
  product: Product;
  onClose: () => void;
}

export function ChatInterface({ product, onClose }: ChatInterfaceProps) {
  const [messages, setMessages] = useState<ChatMessage[]>([]);
  const [input, setInput] = useState("");
  const [isLoading, setIsLoading] = useState(false);
  const [isInitializing, setIsInitializing] = useState(true);
  const [agoraConfig, setAgoraConfig] = useState<{
    appId: string;
    channel: string;
    token: string;
    uid: number;
    rtmUserId: string;
  } | null>(null);
  const [agentId, setAgentId] = useState<string | null>(null);
  const messagesEndRef = useRef<HTMLDivElement>(null);
  const [productContext, setProductContext] = useState<string | null>(null);
  const isInitializingRef = useRef(false);

  // Initialize Agora RTC connection - always call hook but only connect when config is ready
  const agora = useAgora(
    agoraConfig
      ? {
          appId: agoraConfig.appId,
          channel: agoraConfig.channel,
          token: agoraConfig.token,
          uid: agoraConfig.uid,
        }
      : {
          appId: "",
          channel: "",
          token: "",
          uid: 0,
        }
  );

  // Initialize RTM for text messaging
  const rtm = useRTM(
    agoraConfig
      ? {
          appId: agoraConfig.appId,
          userId: agoraConfig.rtmUserId,
          token: agoraConfig.token,
          channel: agoraConfig.channel,
          onMessage: (message: string, senderId: string) => {
            // Only show messages from the agent (UID 1000 or agent ID)
            if (senderId === "1000" || senderId === agentId) {
              setMessages((prev) => [
                ...prev,
                { role: "assistant", content: message },
              ]);
            }
          },
        }
      : {
          appId: "",
          userId: "",
          token: "",
          channel: "",
        }
  );

  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  }, [messages]);

  // Initialize Agora session on mount
  useEffect(() => {
    const initializeAgora = async () => {
      if (isInitializingRef.current) return;
      isInitializingRef.current = true;
      try {
        // Get product context
        const contextRes = await fetch("/api/agora/product-context");
        const contextData = await contextRes.json();
        setProductContext(contextData.context);

        // Initialize Agora session
        const initRes = await fetch("/api/agora/initialize", {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({
            userId: Math.floor(Math.random() * 100000),
          }),
        });

        const initData = await initRes.json();
        setAgoraConfig({
          appId: initData.appId,
          channel: initData.channel,
          token: initData.token,
          uid: initData.uid,
          rtmUserId: initData.rtmUserId || initData.uid.toString(),
        });

        // Start AI agent
        const agentRes = await fetch("/api/agora/start-agent", {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({
            channel: initData.channel,
            userId: initData.uid,
            token: initData.token,
          }),
        });

        if (!agentRes.ok) {
          const errorData = await agentRes.json();
          throw new Error(
            errorData.error || `Failed to start agent: ${agentRes.status}`
          );
        }

        const agentData = await agentRes.json();

        // Store agent_id for chat messages
        if (agentData.agent_id) {
          setAgentId(agentData.agent_id);
        }

        // Set greeting message from ConvoAI if available
        if (agentData.greeting_message) {
          setMessages([
            {
              role: "assistant",
              content: agentData.greeting_message,
            },
          ]);
        } else if (agentData.properties?.llm?.greeting_message) {
          setMessages([
            {
              role: "assistant",
              content: agentData.properties.llm.greeting_message,
            },
          ]);
        }

        setIsInitializing(false);
      } catch (error) {
        console.error("Failed to initialize Agora:", error);
        setIsInitializing(false);
      }
    };

    initializeAgora();

    // Note: cleanup is handled by useAgora hook itself
  }, [product.id]);

  const sendMessage = async () => {
    if (!input.trim() || isLoading || !rtm.isConnected) return;

    const userMessage = input;
    setInput("");
    setMessages((prev) => [...prev, { role: "user", content: userMessage }]);
    setIsLoading(true);

    try {
      // Send message via RTM to agent (using toolkit format)
      await rtm.sendMessage(userMessage, "1000"); // Agent RTM user ID is "1000"
      // Response will come through RTM onMessage callback
      setIsLoading(false);
    } catch (error) {
      console.error("Chat error:", error);
      setMessages((prev) => [
        ...prev,
        {
          role: "assistant",
          content: "Sorry, I encountered an error. Please try again.",
        },
      ]);
      setIsLoading(false);
    }
  };

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === "Enter" && !e.shiftKey) {
      e.preventDefault();
      sendMessage();
    }
  };

  const handleEndCall = () => {
    if (agora) {
      agora.leave();
    }
    onClose();
  };

  const handleClose = () => {
    handleEndCall();
  };

  const handleToggleMute = () => {
    if (agora) {
      agora.toggleMute();
    }
  };

  return (
    <div className="fixed bottom-24 right-6 z-50 w-80">
      <div className="bg-white rounded-lg shadow-2xl border border-gray-200 h-[500px] flex flex-col">
        {/* Header */}
        <div className="flex items-center justify-between p-3 border-b bg-gradient-to-r from-blue-600 to-blue-700 text-white rounded-t-lg">
          <div className="flex items-center gap-2">
            <div className="relative">
              <svg className="w-4 h-4" fill="currentColor" viewBox="0 0 24 24">
                <path d="M12 2a3 3 0 0 0-3 3v6a3 3 0 0 0 6 0V5a3 3 0 0 0-3-3z" />
                <path d="M19 10v1a7 7 0 0 1-14 0v-1M12 18v4M8 22h8" />
              </svg>
              {agora?.isConnected && (
                <div className="absolute -top-1 -right-1 w-2 h-2 bg-green-400 rounded-full animate-pulse"></div>
              )}
            </div>
            <div>
              <h3 className="text-sm font-semibold">Voice Assistant</h3>
              {isInitializing ? (
                <span className="text-xs text-blue-200">Connecting...</span>
              ) : agora?.isConnected ? (
                <span className="text-xs text-green-200">Listening...</span>
              ) : (
                <span className="text-xs text-blue-200">Ready</span>
              )}
            </div>
          </div>
          <button
            onClick={handleClose}
            className="text-white hover:text-gray-200 transition"
            aria-label="Close"
          >
            <svg
              className="w-6 h-6"
              fill="none"
              stroke="currentColor"
              viewBox="0 0 24 24"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={2}
                d="M6 18L18 6M6 6l12 12"
              />
            </svg>
          </button>
        </div>

        {/* Messages */}
        <div className="flex-1 overflow-y-auto p-3 space-y-2 bg-gray-50 text-sm">
          {messages.map((msg, idx) => (
            <div
              key={idx}
              className={`flex ${
                msg.role === "user" ? "justify-end" : "justify-start"
              }`}
            >
              <div
                className={`max-w-[85%] rounded-lg p-2 ${
                  msg.role === "user"
                    ? "bg-blue-600 text-white"
                    : "bg-white text-gray-800 shadow-sm"
                }`}
              >
                <p className="text-xs leading-relaxed">{msg.content}</p>
              </div>
            </div>
          ))}
          {isLoading && (
            <div className="flex justify-start">
              <div className="bg-white text-gray-800 rounded-lg p-2 shadow-sm">
                <div className="flex gap-1">
                  <div className="w-1.5 h-1.5 bg-gray-400 rounded-full animate-bounce"></div>
                  <div
                    className="w-1.5 h-1.5 bg-gray-400 rounded-full animate-bounce"
                    style={{ animationDelay: "0.1s" }}
                  ></div>
                  <div
                    className="w-1.5 h-1.5 bg-gray-400 rounded-full animate-bounce"
                    style={{ animationDelay: "0.2s" }}
                  ></div>
                </div>
              </div>
            </div>
          )}
          <div ref={messagesEndRef} />
        </div>

        {/* Input Area - Voice First, Text as Alternative */}
        <div className="p-3 border-t bg-white rounded-b-lg">
          {/* Primary: Voice Control - Mute/Unmute */}
          <div className="mb-2 flex gap-2">
            <button
              onClick={handleToggleMute}
              disabled={!agora?.isConnected || isInitializing}
              className={`flex-1 py-2 px-2 rounded-lg transition font-medium flex items-center justify-center gap-1 text-xs min-w-0 ${
                agora?.isMuted
                  ? "bg-red-100 text-red-700 hover:bg-red-200 border border-red-300"
                  : "bg-blue-600 text-white hover:bg-blue-700 shadow-md"
              } disabled:opacity-50 disabled:cursor-not-allowed`}
              title={agora?.isMuted ? "Unmute microphone" : "Mute microphone"}
              aria-label={agora?.isMuted ? "Unmute" : "Mute"}
            >
              <svg
                className="w-3.5 h-3.5 flex-shrink-0"
                fill="none"
                stroke="currentColor"
                viewBox="0 0 24 24"
              >
                {agora?.isMuted ? (
                  <>
                    <path
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      strokeWidth={2}
                      d="M5.586 15H4a1 1 0 01-1-1v-4a1 1 0 011-1h1.586l4.707-4.707C10.923 3.663 12 4.109 12 5v14c0 .891-1.077 1.337-1.707.707L5.586 15z"
                    />
                    <path
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      strokeWidth={2}
                      d="M17 14l2-2m0 0l2-2m-2 2l-2-2m2 2l2 2"
                    />
                  </>
                ) : (
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth={2}
                    d="M19 11a7 7 0 01-7 7m0 0a7 7 0 01-7-7m7 7v4m0 0H8m4 0h4m-4-8a3 3 0 01-3-3V5a3 3 0 116 0v6a3 3 0 01-3 3z"
                  />
                )}
              </svg>
              <span className="text-xs whitespace-nowrap truncate">
                {agora?.isMuted ? "Unmute" : "Mute"}
              </span>
            </button>
            <button
              onClick={handleEndCall}
              disabled={isInitializing}
              className="bg-red-600 hover:bg-red-700 disabled:bg-red-400 disabled:cursor-not-allowed text-white py-2 px-2.5 rounded-lg transition font-medium flex items-center justify-center gap-1 shadow-md text-xs flex-shrink-0"
              title="End call"
              aria-label="End call"
            >
              <svg
                className="w-3.5 h-3.5"
                fill="none"
                stroke="currentColor"
                viewBox="0 0 24 24"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={2}
                  d="M16 8l2-2m0 0l2-2m-2 2l-2-2m2 2l2 2M16 8l-8 8m0 0l-2-2m2 2l2-2"
                />
              </svg>
              <span className="text-xs whitespace-nowrap">End</span>
            </button>
          </div>

          {/* Secondary: Text Input (Collapsible) */}
          <details className="text-xs">
            <summary className="cursor-pointer text-gray-500 hover:text-gray-700 mb-1.5">
              Or type a message
            </summary>
            <div className="flex gap-2 mt-2">
              <input
                type="text"
                value={input}
                onChange={(e) => setInput(e.target.value)}
                onKeyPress={handleKeyPress}
                placeholder="Type your question..."
                className="flex-1 border border-gray-300 rounded px-2 py-1.5 text-xs focus:outline-none focus:ring-1 focus:ring-blue-500 focus:border-transparent disabled:opacity-50"
                disabled={isLoading || isInitializing}
              />
              <button
                onClick={sendMessage}
                disabled={isLoading || !input.trim() || isInitializing}
                className="bg-gray-600 hover:bg-gray-700 disabled:bg-gray-400 disabled:cursor-not-allowed text-white px-3 py-1.5 rounded text-xs transition"
              >
                Send
              </button>
            </div>
          </details>
        </div>
      </div>
    </div>
  );
}
