"use client";

import { useState, useEffect, useRef } from "react";

interface UseRTMProps {
  appId: string;
  userId: string;
  token: string;
  channel: string;
  onMessage?: (message: string, senderId: string) => void;
}

export function useRTM({
  appId,
  userId,
  token,
  channel,
  onMessage,
}: UseRTMProps) {
  const [isConnected, setIsConnected] = useState(false);
  const clientRef = useRef<any>(null);

  useEffect(() => {
    if (
      typeof window === "undefined" ||
      !appId ||
      !userId ||
      !token ||
      !channel ||
      !appId.trim() ||
      !userId.trim() ||
      !token.trim() ||
      !channel.trim()
    ) {
      return;
    }

    let isMounted = true;

    const init = async () => {
      try {
        // Dynamically import agora-rtm-sdk only on the client
        const AgoraRTM = await import("agora-rtm-sdk");
        // agora-rtm-sdk exports RTM class - use default export or named export
        const RTM =
          (AgoraRTM.default as any)?.RTM ||
          (AgoraRTM as any).RTM ||
          AgoraRTM.default;
        if (!RTM || typeof RTM !== "function") {
          throw new Error("Failed to get RTM class from agora-rtm-sdk");
        }
        const client = new RTM(appId, userId);
        clientRef.current = client;

        // Debug: Log RTM login parameters
        console.log("[RTM] Login attempt:", {
          appId,
          userId,
          tokenLength: token.length,
          tokenPreview: token.substring(0, 20) + "...",
        });

        // Login to RTM
        await client.login({ token });

        if (!isMounted) {
          await client.logout().catch(() => {});
          return;
        }

        // Listen for peer-to-peer messages (as per ConversationalAIAPI toolkit)
        client.on("MessageFromPeer", (message: any, peerId: string) => {
          if (isMounted && onMessage) {
            try {
              // Parse the message payload (format from toolkit)
              const payload = JSON.parse(message.text);
              if (payload.message) {
                onMessage(payload.message, peerId);
              }
            } catch (e) {
              // Fallback: try to get text directly
              onMessage(message.text || message.message || "", peerId);
            }
          }
        });

        setIsConnected(true);
      } catch (error: any) {
        if (isMounted) {
          console.error("RTM initialization error:", error);
          setIsConnected(false);
        }
      }
    };

    init();

    return () => {
      isMounted = false;

      if (clientRef.current) {
        clientRef.current.logout().catch(() => {});
        clientRef.current = null;
      }

      setIsConnected(false);
    };
  }, [appId, userId, token, channel, onMessage]);

  const sendMessage = async (text: string, agentUserId: string = "1000") => {
    if (!clientRef.current || !isConnected) {
      throw new Error("RTM client not connected");
    }

    try {
      // Use the same format as the ConversationalAIAPI toolkit
      const payload = {
        priority: 1, // EChatMessagePriority.INTERRUPTED
        interruptable: true,
        message: text,
      };

      const payloadStr = JSON.stringify(payload);
      const options = {
        channelType: "USER" as const,
        customType: "user.transcription",
      };

      // Publish to agent user (peer-to-peer, not channel)
      await clientRef.current.publish(agentUserId, payloadStr, options);
    } catch (error) {
      console.error("Error sending RTM message:", error);
      throw error;
    }
  };

  return {
    isConnected,
    sendMessage,
  };
}
